'use client';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, IconButton } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import { deleteParticipante } from '@/services/participantes/participantesService';

export default function ParticipantesTable({ participantes, onRefresh }: any) {
  const handleDelete = async (id: number) => {
    if (confirm('¿Deseas eliminar este participante?')) {
      await deleteParticipante(id);
      onRefresh();
    }
  };

  return (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Nombre Completo</TableCell>
            <TableCell>Departamento</TableCell>
            <TableCell>Municipio</TableCell>
            <TableCell>Acciones</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {participantes.map((p: any) => (
            <TableRow key={p.id}>
              <TableCell>{p.nombreCompleto}</TableCell>
              <TableCell>{p.departamento}</TableCell>
              <TableCell>{p.municipio}</TableCell>
              <TableCell>
                <IconButton color="primary">
                  <EditIcon />
                </IconButton>
                <IconButton color="error" onClick={() => handleDelete(p.id)}>
                  <DeleteIcon />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
